.cards {
    justify-content: center;
    margin: 20px ;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    grid-auto-rows: auto;
    gap: 20px;
    font-family: sans-serif;
    padding-top: 30px;
    margin-bottom: 100px;
}
@media only screen and (min-width: 900px) {
    .cards {
        grid-template-columns: repeat(3, minmax(350px, 400px));
    }
   
}

.cards * {
    box-sizing: border-box;
}


.card__image {
    width: 100%;
    height: 350px;
    object-fit: cover;
    display: block;
   
}

.card__content {
    line-height: 1.5;
    font-size: 0.9em;
    padding: 15px;
    background: #fafafa;
  
    
}

.card__content > p:first-of-type {
    margin-top: 0;
}

.card__content > p:last-of-type {
    margin-bottom: 0;
}

.card__info {
    padding: 15px;
    padding-bottom: 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #555555;
    background: #eeeeee;
    font-size: 0.8em;
    
}

.card__info i {
    font-size: 0.9em;
    margin-right: 8px;
    margin-bottom: 0px !important;
}

.card__link {
    color: #64968c;
    text-decoration: none;
    margin-bottom: 0px;
    padding-bottom: 0px;
}

.card__link:hover {
    text-decoration: underline;
}